@use "./variables" as *;

@mixin mobile {
    @media only screen and (max-width: $mobile-width) {
        @content;
    }
}

@mixin tablet {
    @media only screen and (max-width: $tablet-width) {
        @content;
    }
}


@mixin header__mobile {
    @media only screen and (max-width: 700px) {
        @content;
    }
}